___CSS_LOADER_EXPORT___.push([module.id, `.form-container {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  padding: 24px;
  max-width: 700px;
  margin: 0 auto;
  font-family: 'Segoe UI', Arial, sans-serif;
}

.form-header {
  margin-bottom: 24px;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: #f3f4f6;
  border-radius: 4px;
  margin-bottom: 8px;
  overflow: hidden;
}
.progress-fill {
  height: 100%;
  background: #3b82f6;
  border-radius: 4px;
  transition: width 0.3s;
}
.progress-text {
  font-size: 1rem;
  color: #555;
  margin-bottom: 16px;
}

.form-field {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}
.field-label {
  font-weight: 600;
  margin-bottom: 4px;
  display: flex;
  align-items: center;
}
.field-input {
  padding: 10px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 1rem;
}
.input-feedback {
  color: #ef4444;
  font-size: 0.92rem;
  margin-top: 2px;
  display: none;
}
.field-valid .input-feedback {
  color: #10b981;
}
.field-help {
  font-size: 0.92rem;
  color: #888;
  margin-top: 2px;
}
.btn-primary {
  background: #3b82f6;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}
.btn-primary:disabled {
  background: #a5b4fc;
  cursor: not-allowed;
}
.btn-secondary {
  background: #f3f4f6;
  color: #1e293b;
  border: 1px solid #cbd5e1;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.75em 1.5em;
  margin: 0 0 16px 0;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}
.btn-secondary:hover, .btn-secondary:focus {
  background: #e0e7ef;
  color: #2563eb;
  outline: 2px solid #2563eb;
}
.btn-loader {
  display: inline-block;
  margin-left: 8px;
}
.tooltip {
  position: absolute;
  background: #222;
  color: #fff;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 0.95rem;
  z-index: 1000;
  pointer-events: none;
}
.help-icon {
  margin-left: 6px;
  color: #2563eb;
  cursor: pointer;
  font-size: 1.1rem;
}
.return-home-btn {
  float: right;
  margin-top: 0;
  margin-bottom: 16px;
}
@media (max-width: 600px) {
  .form-container { padding: 10px; }
  .form-field { margin-bottom: 14px; }
  .btn-primary, .btn-secondary { width: 100%; margin-bottom: 8px; }
}

.form-fields-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px 32px;
  margin-bottom: 24px;
}
@media (max-width: 700px) {
  .form-fields-grid {
    grid-template-columns: 1fr;
    gap: 20px 0;
  }
} `, "",{"version":3,"sources":["webpack://./src/components/shared-form-styles.css"],"names":[],"mappings":"AAAA;EACE,gBAAgB;EAChB,mBAAmB;EACnB,sCAAsC;EACtC,aAAa;EACb,gBAAgB;EAChB,cAAc;EACd,0CAA0C;AAC5C;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,WAAW;EACX,mBAAmB;EACnB,kBAAkB;EAClB,kBAAkB;EAClB,gBAAgB;AAClB;AACA;EACE,YAAY;EACZ,mBAAmB;EACnB,kBAAkB;EAClB,sBAAsB;AACxB;AACA;EACE,eAAe;EACf,WAAW;EACX,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;EACnB,aAAa;EACb,sBAAsB;AACxB;AACA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,aAAa;EACb,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,yBAAyB;EACzB,kBAAkB;EAClB,eAAe;AACjB;AACA;EACE,cAAc;EACd,kBAAkB;EAClB,eAAe;EACf,aAAa;AACf;AACA;EACE,cAAc;AAChB;AACA;EACE,kBAAkB;EAClB,WAAW;EACX,eAAe;AACjB;AACA;EACE,mBAAmB;EACnB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,iBAAiB;EACjB,gBAAgB;EAChB,eAAe;EACf,2BAA2B;AAC7B;AACA;EACE,mBAAmB;EACnB,mBAAmB;AACrB;AACA;EACE,mBAAmB;EACnB,cAAc;EACd,yBAAyB;EACzB,kBAAkB;EAClB,eAAe;EACf,gBAAgB;EAChB,qBAAqB;EACrB,kBAAkB;EAClB,eAAe;EACf,uCAAuC;AACzC;AACA;EACE,mBAAmB;EACnB,cAAc;EACd,0BAA0B;AAC5B;AACA;EACE,qBAAqB;EACrB,gBAAgB;AAClB;AACA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,WAAW;EACX,iBAAiB;EACjB,kBAAkB;EAClB,kBAAkB;EAClB,aAAa;EACb,oBAAoB;AACtB;AACA;EACE,gBAAgB;EAChB,cAAc;EACd,eAAe;EACf,iBAAiB;AACnB;AACA;EACE,YAAY;EACZ,aAAa;EACb,mBAAmB;AACrB;AACA;EACE,kBAAkB,aAAa,EAAE;EACjC,cAAc,mBAAmB,EAAE;EACnC,+BAA+B,WAAW,EAAE,kBAAkB,EAAE;AAClE;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,cAAc;EACd,mBAAmB;AACrB;AACA;EACE;IACE,0BAA0B;IAC1B,WAAW;EACb;AACF","sourcesContent":[".form-container {\r\n  background: #fff;\r\n  border-radius: 12px;\r\n  box-shadow: 0 2px 8px rgba(0,0,0,0.06);\r\n  padding: 24px;\r\n  max-width: 700px;\r\n  margin: 0 auto;\r\n  font-family: 'Segoe UI', Arial, sans-serif;\r\n}\r\n\r\n.form-header {\r\n  margin-bottom: 24px;\r\n}\r\n\r\n.progress-bar {\r\n  width: 100%;\r\n  height: 8px;\r\n  background: #f3f4f6;\r\n  border-radius: 4px;\r\n  margin-bottom: 8px;\r\n  overflow: hidden;\r\n}\r\n.progress-fill {\r\n  height: 100%;\r\n  background: #3b82f6;\r\n  border